<!-- by 添少 -->
<template>
  <section>
    <section class="preview">
      <div class="cover">
        <img class="db fit-cover" :src="detail.mainimg"/>
      </div>
      <div class="text">
        <h3>{{detail.name}}</h3>
        <p>{{detail.desc}}</p>
      </div>
      <div class="ft">
        <template v-if="detail.joinSatus===1">
          <button class="ui-button primary" @click="handJoin">加入俱乐部</button>
        </template>
        <template v-else>
          <button class="ui-button disabled">已加入俱乐部</button>
        </template>
      </div>
    </section>
  </section>
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    name: 'club',
    components: {},
    data() {
      return {
        detail: {},
      }
    },
    computed: {
      ...mapState({}),
      tabList() {
        let list = []
        this.tab.list.forEach(e => list.push(e.label))
        return list
      }
    },
    created() {
      this.init()
    },
    methods: {
      init() {
        this.$store.commit(this.$actionType.set_detail, {
          title: '俱乐部',
          id: this.$route.query.clubId,
          userId: this.detail.userId,
        })
        if (this.$route.query.clubId) {
          this.$http.get(this.$port.GET_CLUB_DETAIL, {
            params: {
              clubId: this.$route.query.clubId
            }
          }).then(res => {
            if (res.code === 200) {
              this.detail = res.data
            } else {
              alert(res.msg)
            }
          })
        }
      },
      handJoin() {
        this.$http.post(this.$port.MY_CLUB_JOIN, this.$qs.stringify({
          clubId: this.$route.query.clubId,
        })).then(res => {
          if (res.code === 200) {
            this.detail.joinSatus = 2
          }
          alert(res.msg)
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }

  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px;
      margin-bottom: 10px;
    }
    .cover {
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 5px;
    }
    .ft {
      text-align: center;
      padding-bottom: 15px;
    }
  }
</style>



